import Vue from "vue";
import Router from "vue-router";
import MIndex from "./views/MIndex.vue";
import Home from "./views/Home.vue";
import Register from "./views/Register.vue";
import RePsw from "./views/RePsw.vue";
import SetInfo from "./views/SetInfo.vue";
import CourseCenter from "./views/CourseCenter.vue";
import CourseList from "./views/CourseList.vue";
import CourseDetails from "./views/CourseDetails.vue";
import Learn from "./views/Learn.vue";
import OrderDetails from "./views/OrderDetails";
import ConfirmOrder from "./views/ConfirmOrder";
import OrderCenter from "./views/OrderCenter";
import Live from "./views/Live.vue";
import QuestionBank from "./views/QuestionBank.vue";
import LearningSpace from "./views/LearningSpace.vue";

Vue.use(Router);

export default new Router({
    mode: "history",
    base: process.env.BASE_URL,
    routes: [
        {
            path: "/",
            name: "home",
            component: Home
        },
        {
            path: '/mIndex', // 手机端首页
            name: "mIndex",
            component: MIndex
        },
        {
            path: "/register",
            name: "register",
            component: Register
        },
        {
            path: "/rePsw",
            name: "rePsw",
            component: RePsw
        },
        {
            path: "/setInfo",
            name: "setInfo",
            component: SetInfo
        },
        {
            path: "/courseCenter",
            name: "courseCenter",
            component: CourseCenter,
            redirect: "/courseCenter/courseList/0",
            children: [
                {
                    // 课程列表参数要求：一级领域id
                    path: "courseList/:domainCategoryId",
                    name: "courseList",
                    component: CourseList,
                    props: true
                },
                {
                    path: "courseDetails/:curriculumId",
                    name: "courseDetails",
                    component: CourseDetails,
                    props: true
                }
            ]
        },
        {
            // 课程学习页面参数要求：课程id，章节id
            path: "/learn/:curriculumId/:chapterId",
            name: "learn",
            component: Learn,
            props: true
        },
        {
            path: "/confirmOrder/:curriculumId",
            name: "confirmOrder",
            component: ConfirmOrder,
            props: true
        },
        {
            path: "/orderDetails/:orderNumber",
            name: "orderDetails",
            component: OrderDetails,
            props: true
        },
        {
            path: "/orderCenter",
            name: "orderCenter",
            component: OrderCenter,
        },
        {
            path: "/live",
            name: "live",
            component: Live
        },
        {
            path: "/questionBank",
            name: "questionBank",
            component: QuestionBank
        },
        {
            path: "/learningSpace",
            name: "learningSpace",
            component: LearningSpace
        }
    ]
});
