// 自定义的补充方法
const CustomPatch = {
  /**
   * 节流函数
   * @param method 要执行的方法
   * @param delay 必定触发的时间间隔
   * @param time 连续触发的时间间隔
   * @returns {Function}
   */
  throttle(method, delay, time) {
    let timeout, startTime = new Date();
    return function () {
      let context = this,
        args = arguments,
        curTime = new Date();
      clearTimeout(timeout);
      // 如果达到了规定的触发时间间隔，触发 handler
      if (curTime - startTime >= time) {
        method.apply(context, args);
        startTime = curTime;
        // 没达到触发间隔，重新设定定时器，保证必定触发一次
      } else {
        timeout = setTimeout(method, delay);
      }
    }
  }
};
export default CustomPatch;
