import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 全局请求响应错误信息
    httpState: String,
    // 判断断网
    network: Boolean,
    // 登录状态
    loginStatus: false,
    // 右侧板块相关
    // 右侧显示
    isShowRight:false,
    // 右侧完全显示
    isShowAllRight:false,
    // 全局搜索参数
    searchContent: "",
    // 是否在非登录情况下更新用户信息（如修改资料）,参数的真或假无实际意义，只用于侦听状态改变
    isUpdateUserIfo: false,

    // 控制首页模块淡出动画
    // 领域导航
    showSortNav: false,
    //课程导航
    showCourseNav: false,
    // 证书板块
    showCertificate: false,
  },
  mutations: {
    changeHttpState(state, value) {
      state.httpState = value;
    },
    /**
     * 改变断网状态
     * @param state  {Object}  状态
     * @param value {Boolean} 状态值
     */
    changeNetwork(state, value) {
      state.network = value;
    },

    // 右侧板块相关
    /**
     *  改变右侧显示
     * @param state {Object}  状态
     * @param value {Boolean} 状态值
     */
    changeIsShowRight(state, value) {
      state.isShowRight = value;
    },
    /**
     *  改变右侧完全显示
     * @param state {Object}  状态
     * @param value {Boolean} 状态值
     */
    changeIsShowAllRight(state, value) {
      state.isShowAllRight = value;
    },

    /**
     *  改变登录状态
     * @param state {Object}  状态
     * @param value {Boolean} 状态值
     */
    changeLoginStatus(state, value) {
      state.loginStatus = value;
    },
    /**
     * 改变全局搜索内容
     * @param state
     * @param value
     */
    changeSearchContent(state, value) {
      state.searchContent = value;
    },
    /**
     * 改变更新用户信息状态
     * @param state
     * @param value
     */
    changeIsUpdateUserIfo(state, value) {
      state.isUpdateUserIfo = value;
    },
    /**
     * 改变首页领域快捷入口显示状态
     * @param state
     * @param value
     */
    changeShowSortNav(state, value) {
      state.showSortNav = value;
    },
    /**
     * 改变首页课程快捷入口显示状态
     * @param state
     * @param value
     */
    changeShowCourseNav(state, value) {
      state.showCourseNav = value;
    },
    /**
     * 改变证书板块显示状态
     * @param state
     * @param value
     */
    changeShowCertificate(state, value) {
      state.showCertificate = value;
    },
  },
  actions: {}
});
