<template>
  <div class="CHeader">
    <div class="header_item" style="margin-right: 40px;">
      <router-link to="/" exact id="logo-container">
        <img src="../common/images/logo.png" alt="大梁造教育" class="logo">
      </router-link>
      <transition name="fade-move">
        <div class="header_item" v-show="!isShowAllRight">
          <!-- 搜索功能 -->
          <div id="search_form">
            <input type="text" name="search" class="searchInput" autocomplete="off" v-model="searchText"
                   @keyup.enter="runSearch">
            <button class="search-button" @click="runSearch">
              <font-awesome-icon :icon="['fas','search']" class="search-icon"/>
            </button>
          </div>
          <router-link to="/" exact class="router" id="to-home">
            <div class="icon-container">
              <font-awesome-icon :icon="['fas','home']" class="router-icon"/>
            </div>
            <span>首页</span>
          </router-link>
          <router-link to="/courseCenter" class="router" id="to-subList">
            <div class="icon-container">
              <font-awesome-icon :icon="['fas','school']" class="router-icon"/>
            </div>
            <span>课程中心</span>
          </router-link>
        </div>
      </transition>


    </div>
    <!-- 顶部导航1 -->
    <div class="header_item" style="width: 400px;flex-shrink: 0;justify-content: flex-end;">
      <!-- <div style="width: 150px;" v-show="isShowAllRight"></div>-->
      <!-- 顶部导航2 -->
      <ul class="nav">
        <!-- 登录前 -->
        <li @click="closeLogin" v-if="isLogin===false" style="margin-right: 10px;">
          <router-link to="/register" class="router">
            <div class="icon-container">
              <font-awesome-icon :icon="['fas','registered']" class="router-icon"/>
            </div>
            <span>注册</span>
          </router-link>
        </li>
        <li class="nav_item" id="nav_login" v-if="isLogin===false" @click="isShowLogin">
          <font-awesome-icon :icon="['fas','user-circle']" class="login-icon"/>
        </li>
        <!-- 登录后 -->
        <li class="nav_item" id="loginAfter" v-else>

          <!-- 用户信息 -->
          <img :src="userInfo.photo" :alt="userInfo.nickName" class="avatar" @click="isShowLogin">
<!--          订单中心-->
          <div class="header_item">
            <router-link to="/orderCenter" class="router">
              <div class="icon-container">
                <font-awesome-icon :icon="['fas','align-justify']" class="router-icon"/>
              </div>
              <span>订单中心</span>
            </router-link>
          </div>

        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "CHeader",
  props: {
    isShowAllRight: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 搜索插件
      searchText: "",
      results: [],
      isShowLoginDL: false,
      userInfo: {
        phoneNumber: "",
        nickName: "",
        realName: null,
        userSex: 0,
        accounts: 0,
        idNumber: null,
        educationalStatus: 0,
        domainList: [
          {
            id: "",
            domainName: "",
            isSelected: false
          }
        ],
        status: 0,
        photo: 0,
        lastLoginTime: "",
        personalSign: null
      }
    };
  },
  computed: {
    isLogin() {
      return this.$store.state.loginStatus;
    },
    isUpdateUserIfo() {
      return this.$store.state.isUpdateUserIfo;
    }
  },
  watch: {
    /**
     * 侦听登录状态，更新用户登录信息
     */
    isLogin: function () {
      if (localStorage.getItem("DLZEdToken")) {
        this.userInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
      }
    },
    /**
     * 侦听用户数据更新状态，更新用户信息
     */
    isUpdateUserIfo: function () {
      this.userInfo = JSON.parse(localStorage.getItem("DLZEdUserModel"));
    }
  },
  methods: {
    runSearch() {
      // vuex改变全局搜索文本
      this.$store.commit("changeSearchContent", this.searchText);
      // 根据当前路由地址改变搜索跳转方式
      if (this.$route.path.indexOf("/courseCenter/courseList") !== -1) {
        this.$router.replace({path: `/courseCenter/courseList/0`});
      } else {
        this.$router.push({path: `/courseCenter/courseList/0`});
      }
    },
    // 控制登录窗口显示隐藏
    isShowLogin() {
      this.$emit("toggle");
      this.$emit("closeAllShow");
    },
    // 关闭右侧栏
    closeLogin() {
      this.$emit("closeRight");
      this.$emit("closeAllShow");
    },
    // 登出
    signOut() {
      localStorage.removeItem("DLZEdToken");
      localStorage.removeItem("DLZEdUserModel");
      // vuex改变全局登录状态
      this.$store.commit("changeLoginStatus", false);
    }
  },
  components: {},
  mounted() {

  }
};
</script>

<style scoped>
@import "../common/css/CHeader.css";
</style>
