<template>
  <div class="CourseList">
    <div id="plate-header">
      <div class="plate-header-content">
        <div class="plate-header-title">
          课程中心
        </div>
      </div>
    </div>
    <div id="plate-course">
      <!--筛选示方式-->
      <div class="filter">
      </div>
      <!--课程分类-->
      <div class="classNav">
        <dl class="sort">
          <dt class="sort-title">领域</dt>
          <transition-group tag="div" name="list" class="transition-group-list">
            <dd v-for="(item,index) in domainList1"
                class="sort-content" :class="{'sort-active':sort1Position === index}"
                :key="item.domainCategoryId"
                @click="sortToggle(index,item.domainCategoryId,item.parentId)">
              {{item.domainName}}
            </dd>
          </transition-group>
        </dl>
        <dl class="sort">
          <dt class="sort-title">分类</dt>
          <transition-group name="list" tag="div" class="transition-group-list">
            <dd class="sort-content" :class="{'sort-active':sort2Position === index}"
                v-for="(item,index) in domainList2"
                :key="item.domainCategoryId"
                @click="sortToggle(index,item.domainCategoryId,item.parentId)">
              {{item.domainName}}
            </dd>
          </transition-group>
        </dl>
      </div>
      <!--课程列表-->
      <!--        根据网络获取的原始课程列表判断，本地监听处理过的课程列表会附加属性，不可被判空,且不可使用强类型判断-->
      <transition name="fade" tag="div">
        <div class="noCourse" v-show="temCourseList==''">暂无相关课程</div>
      </transition>

      <transition-group name="list" tag="div" class="course-list" v-show="temCourseList!==''">
        <router-link tag="div" :to="item.path" class="course-item" v-for="item in courseList"
                     :key="item.curriculumId">
          <div style="overflow: hidden">
            <img :src="item.thumbnailImage" alt="" class="course-img">
          </div>
          <div class="course-content">
            <div class="course-tit">
              <h2>{{item.curriculumName}}</h2>
            </div>
            <div class="course-price">
              <!--<i class="course-price-sale"></i>-->
              <span class="course-price-now">
                <i>￥</i>
                {{item.realPrice}}
              </span>
              <span class="course-price-cost">
                <i>￥</i>
                {{item.price}}
              </span>
            </div>
          </div>
        </router-link>

      </transition-group>
      <!--分页器-->
      <el-row type="flex" justify="center" v-show="temCourseList!=''">
        <el-pagination
          @current-change="clickCallback"
          :page-count="pageCount"
          :current-page="currentPage"
          layout="prev, pager, next, jumper"
        >
        </el-pagination>
      </el-row>
    </div>
    <div id="plate-marketing"></div>
  </div>
</template>

<script>
export default {
  name: "CourseList",
  props: ["domainCategoryId"],
  components: {},
  data() {
    return {
      items: [1, 2, 3, 4, 5, 6, 7, 8, 9],
      nextNum: 10,
      // 领域列表
      domainList1: [],
      domainList2: [],
      // 控制领域列表点击样式切换
      sort1Position: 0,
      sort2Position: 0,
      // 原始课程列表
      temCourseList: [],
      // 带路径的课程列表
      courseList: [],
      // 缓存课程搜索相关参数
      _searchContent: "",
      _domains: "",
      _sortType: "",
      _pageNum: "",
      pageCount: 1,// 分页参数
      currentPage: 1,// 当前页码
    };
  },
  computed: {
    searchContent() {
      return this.$store.state.searchContent;
    }
  },
  watch: {
    searchContent: function () {
      this.sortToggle(0, "0", null);
    },
    temCourseList: function (val) {
      for (let i = 0; i < val.length; i++) {
        val[i].path = "/courseCenter/courseDetails/" + val[i].curriculumId;
      }
      this.courseList = val;
    }
  },
  methods: {
    randomIndex: function () {
      return Math.floor(Math.random() * this.items.length)
    },
    add: function () {
      this.items.splice(this.randomIndex(), 0, this.nextNum++)
    },
    remove: function () {
      this.items.splice(this.randomIndex(), 1)
    },
    shuffle: function () {
      this.items = _.shuffle(this.items)
    },
    // 获取领域列表方法
    getDomainList() {
      this.$api.curriculum
        .getDomainList()
        .then(res => {
          this.domainList1 = res.data.data;
          // 初次渲染领域列表
          this.sortToggle(0, this.domainCategoryId, null);
        })
        .catch(error => {
          console.log(error);
        });
    },
    /**
     * 分类筛选
     * @param position2 {Number} 二级分类页面控制参数
     * @param domainCategoryId  {String} 分类id
     * @param parentId  {String}  父级分类id
     */
    sortToggle(position2, domainCategoryId, parentId) {
      // 领域相关参数全为默认值时(即根据文本搜索时）给获取课程列表方法传入搜索文本
      if (position2 === 0 && domainCategoryId === "0" && parentId === null) {
        this.getCurriculumList("0", 1, this.searchContent, domainCategoryId);
      } else {
        // 按领域查询时不传入搜索文本
        this.getCurriculumList("0", 1, "", domainCategoryId);
      }
      if (parentId === null) {
        for (let i = 0; i < this.domainList1.length; i++) {
          if (this.domainList1[i].domainCategoryId === domainCategoryId) {
            // 改变二级分类列表
            this.domainList2 = this.domainList1[i].domainList;
            // 控制active样式
            this.sort1Position = i;
            // 将二级分类列表重置为全部
            this.sort2Position = 0;
          }
        }
      } else {
        // 根据二级列表选择项的parentId判断属于哪个一级类目，从而渲染二级分类列表
        for (let n = 0; n < this.domainList1.length; n++) {
          if (this.domainList1[n].domainCategoryId === parentId) {
            // 改变二级分类列表
            this.domainList2 = this.domainList1[n].domainList;
            // 控制一级分类active样式
            this.sort1Position = n;
            // 重新对新的二级分类列表进行遍历，判断正确的二级列表样式定位
            for (let m = 0; m < this.domainList2.length; m++) {
              if (this.domainList2[m].domainCategoryId === domainCategoryId) {
                this.sort2Position = m;
              }
            }
          }
        }
      }
    },
    /**
     *获取课程列表
     * @param sortType {String} 排序类型 0.默认 1.创建时间 2.热度（购买次数）必填
     * @param pageNum {Number}  页码 默认:1
     * @param searchContent {String} 内容筛选 默认:''
     * @param domains {String}  领域筛选 默认:'0'
     */
    getCurriculumList(sortType, pageNum, searchContent, domains) {
      // 文本搜索课程
      if (searchContent !== "") {
        this._domains = "";
        this._searchContent = searchContent;
        this._sortType = "0";
        this._pageNum = 1;
        let params = {
          "Condition.SearchContent": this._searchContent,
          "Condition.SortType": this._sortType,
          "Condition.Domains": this._domains,
          "Condition.PageNum": this._pageNum
        };
        this.$api.curriculum
          .getCurriculumList(params)
          .then(res => {
            this.temCourseList = res.data.data.dataList;
            let totalCount = res.data.data.page.totalCount,
              pageSize = res.data.data.page.pageSize;
            this.pageCount = Math.ceil(totalCount / pageSize);
          })
          .catch(error => {
            console.log(error);
          });
      } else if (domains !== "") {
        // 分类搜索课程
        this._domains = domains;
        this._searchContent = "";
        this._sortType = "0";
        this._pageNum = 1;
        let params = {
          "Condition.SearchContent": this._searchContent,
          "Condition.SortType": this._sortType,
          "Condition.Domains": this._domains,
          "Condition.PageNum": this._pageNum
        };
        this.$api.curriculum
          .getCurriculumList(params)
          .then(res => {
            this.temCourseList = res.data.data.dataList;

            // 设置分页参数
            let totalCount = res.data.data.page.totalCount,
              pageSize = res.data.data.page.pageSize;
            this.pageCount = Math.ceil(totalCount / pageSize);
          })
          .catch(error => {
            console.log(error);
          });
      } else if (sortType !== "") {
        // 排序搜索课程
        this._sortType = sortType;
        this._pageNum = 1;
        let params = {
          "Condition.SearchContent": this._searchContent,
          "Condition.SortType": this._sortType,
          "Condition.Domains": this._domains,
          "Condition.PageNum": this._pageNum
        };
        this.$api.curriculum
          .getCurriculumList(params)
          .then(res => {
            this.pageCount = res.data.data.page.totalCount;
          })
          .catch(error => {
            console.log(error);
          });
      } else if (pageNum !== "") {
        // 页码搜索课程
        this._pageNum = pageNum;
        let params = {
          "Condition.SearchContent": this._searchContent,
          "Condition.SortType": this._sortType,
          "Condition.Domains": this._domains,
          "Condition.PageNum": this._pageNum
        };
        this.$api.curriculum
          .getCurriculumList(params)
          .then(res => {
            this.temCourseList = res.data.data.dataList;
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    /**
     * 分页按钮点击回调函数
     * @param page {Number} 当前页码
     */
    clickCallback(page) {
      this.getCurriculumList("", "", "", page);
    }
  },
  mounted() {
    // 获取领域列表
    this.getDomainList();
  }
};
</script>
<style scoped src="../common/css/CourseList.css">
</style>
