<template>
  <div class="ConfirmOrder">
    <!--头部-->
    <div class="confirmOrder-header">
      <div class="confirmOrder-header-content">
        <div class="toCourseDetails-nav" @click="goBack">
          <font-awesome-icon :icon="['fas','arrow-left']" class="back-icon"/>
          <span>返回</span>
        </div>
        <div class="confirmOrderTitle">
          确认订单
        </div>
      </div>
    </div>
    <!--    订单信息-->
    <div class="confirmOrder-body">
      <div class="title-box">
        <p>商品信息</p>
      </div>
      <div class="details-box">
        <img :src="curriculumInfo.image" :alt="curriculumInfo.curriculumName" class="order-details-img">
        <div class="order-details-content">
          <p class="order-details-name">{{curriculumInfo.curriculumName}}</p>
        </div>
        <p class="order-details-price">
          <em>￥</em>
          <span class="price-number">{{curriculumInfo.realPrice}}</span>
        </p>
      </div>
      <div class="coupons-box"></div>
      <div class="pay-box">
        <div class="goods-total-price-box">
          <p class="price-text">商品总金额：</p>
          <p class="price-num">￥{{curriculumInfo.price}}</p>
        </div>
        <div class="package-discount-box">
          <p class="price-text">优惠折扣：</p>
          <p class="price-num">折扣信息缺失</p>
        </div>
        <div class="pay-price-box">
          <p class="price-text">应付：</p>
          <p class="price">￥{{curriculumInfo.realPrice}}</p>
        </div>
        <button id="submitOrder-btn" @click="submitOrder">提交订单</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ConfirmOrder",
  props: ["curriculumId"],
  data() {
    return {
      curriculumInfo: {}
    };
  },
  components: {},
  methods: {
    // 获取课程详情
    getCurriculumModel() {
      let data = {
        "Data.CurriculumId": this.curriculumId,
        Version: ""
      };
      this.$api.curriculum
        .getCurriculumModel(data)
        .then(res => {
          console.log(res.data.data);
          this.curriculumInfo = res.data.data;
        })
        .catch(error => {});
    },
    // 提交订单
    submitOrder() {
      let data = {
        data: {
          curriculumId: this.curriculumId,
          deductAccount: 0,
          gapCause: "string",
          platform: 0
        },
        version: "string"
      };
      this.$api.order.createOrder(data).then(res => {
        console.log(res);
        this.$router.push({
          path: `/orderDetails/${res.data.data.orderNumber}`
        });
      });
    },
    // 返回上一页
    goBack() {
      this.$router.back();
    }
  },
  mounted() {
    this.getCurriculumModel();
  }
};
</script>
<style scoped src="../common/css/ConfirmOrder.css">
</style>
