<template>
  <div class="OrderDetails">
    <!--头部-->
    <div class="order-header">
      <div class="order-header-content">
        <div class="toConfirmOrder-nav" @click="goBack">
          <font-awesome-icon :icon="['fas','arrow-left']" class="back-icon"/>
          <span>返回</span>
        </div>
        <div class="orderTitle">
          订单详情
        </div>
      </div>
    </div>
    <!--订单详情-->
    <div class="order-body">
      <div class="title-box">订单编号：{{orderDetails.orderNumber}}</div>
      <div class="details-box">
        <img :src="orderDetails.thumbnailImage" :alt="orderDetails.curriculumName" class="order-details-img">
        <div class="order-details-content">
          <p class="order-details-name">{{orderDetails.curriculumName}}</p>
        </div>
        <p class="order-details-price">
          <span>金额</span>
          <em>￥</em>
          <span class="price-number">{{orderDetails.realPrice}}</span>
        </p>
        <p class="order-details-state">
          <span>支付状态：</span>
          <span class="state-noPay">{{orderDetails.orderState.value}}</span>
        </p>
      </div>
      <div class="pay-box" v-show="orderDetails.orderState.key===0">
        <h2 class="pay-title">支付方式</h2>
        <ul class="payType-list">
          <li class="payType" id="pay-wChart">微信支付</li>
        </ul>
      </div>
      <div id="pay-btn" @click="orderPay" v-show="orderDetails.orderState.key===0">立即支付</div>
    </div>
    <!--    支付二维码-->
    <div class="QRCode-bg" v-show="isShowPayQR">

      <div class="QRCode-container">
        <p class="QR-title">扫码完成支付</p>
        <div class="QRCode-box">
          <qriously :value="QRCode" :size="200"/>
        </div>
        <p class="QR-tip">支付时请勿关闭窗口</p>
        <div class="button-box">
          <button class="QR-button-ok" @click="completePay">已完成支付</button>
          <button class="QR-button-no" @click="closePay">取消支付</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {SnotifyPosition, SnotifyToast, SnotifyToastConfig} from "vue-snotify";
  export default {
    name: "OrderDetails",
    props: ["orderNumber"],
    data() {
      return {
        // 订单详情
        orderDetails: {
          orderState:{
            key:0,
            value:'待支付'
          }
        },
        isShowPayQR: false,
        QRCode: "",
        // 判断支付状态循环定时器
        payInterval:Object
      };
    },
    components: {},
    methods: {
      // 获取课程详情,并重定向至学习页面
      getCurriculumAndReplace() {
        let data = {
          "Data.CurriculumId": this.curriculumId,
          Version: ""
        };
        this.$api.curriculum
          .getCurriculumModel(data)
          .then(res => {
            // 缓存章节列表用于跳转传值
            this.chapterList = res.data.data.chapterList;
            // 重定向至学习页面
            this.$router.replace({
              path: `/learn/${this.orderDetails.curriculumId}/${this.chapterList[0].subList[0].chapterId}`
            });
          })
          .catch(error => {
          });
      },
      // 开启支付
      orderPay(){
        // let that=this;
        let data = {
          data: {
            orderNumber: this.orderNumber,
            paymentType: 2
          },
          version: "string"
        };
        this.$api.payment.webPay(data).then(res => {
          this.QRCode = res.data.data;
          this.isShowPayQR = true;
        });
        // 每两秒检测订单详情判断支付状态
        this.payInterval = setInterval(() => {
          console.log("更新订单数据");
          this.$api.order.getOrderDetails(this.orderNumber).then(res => {
            if (res.data.data.orderState.key ===1){
              this.$snotify.success("支付成功");
              this.isShowPayQR = false;
              // 清除定时器
              clearInterval(this.payInterval);
              // 跳转学习页面
              this.getCurriculumAndReplace();
            }
            this.orderDetails = res.data.data;
          });
        }, 2000)
      },
      // 完成支付
      completePay() {
        this.$api.order.getOrderDetails(this.orderNumber).then(res => {
          this.orderDetails = res.data.data;
          if (res.data.data.orderState.key === 1) {
            this.$snotify.success("支付成功");
            this.isShowPayQR = false;
            // 清除定时器
            clearInterval(this.payInterval);
            // 跳转学习页面
            this.getCurriculumAndReplace();
          }else{
            this.$snotify.error("您未完成支付");
          }
        });
      },
      // 关闭支付
      closePay() {
        this.isShowPayQR = false;
        // 清除定时器
        clearInterval(this.payInterval);
      },
      /**
       * 显示订单详情
       * @param orderNumber {Number} 订单编号
       */
      showOrderDetail(orderNumber) {
        this.$api.order.getOrderDetails(orderNumber).then(res => {
          console.log(res);
          this.orderDetails = res.data.data;
        });
      },
      // 返回上一页
      goBack() {
        this.$router.back();
      }
    },
    mounted() {
      this.showOrderDetail(this.orderNumber);
    }
  };
</script>
<style scoped src="../common/css/OrderDetails.css">
</style>
