<template>
    <div id="app">
        <div id="Header" v-if="!isMobile">
            <c-header @toggle="rightToggle" @closeRight="closeRight" @closeAllShow="closeAllShow"
                      v-bind:isShowAllRight="isShowAllRight"></c-header>
        </div>

        <!--<div class="left"-->
        <!--<div class="sidebar-title-left">功能左侧栏</div>-->
        <!--</div>-->
        <transition name="fade">
            <router-view class="router-view" :style="{height: screenHeight}"/>
        </transition>


        <c-right class="right" v-if="!isMobile"
                 :class="{showRight:isShowRight,showAllRight:isShowAllRight}"
                 v-bind:style="{right:showRightPosition}"
                 @toggle="rightToggle" @allShow="allShow" @closeAllShow="closeAllShow" v-bind:mActive="rightSvgStatus">
        </c-right >
        <div id="footer" v-if="!isMobile">
            <c-foot></c-foot>

        </div>
        <transition name="mask">
            <div class="mask" v-show="isShowRight" @click="rightToggle"></div>
        </transition>
        <!--提示插件-->
        <vue-snotify></vue-snotify>
    </div>
</template>
<script>
// @ is an alias to /src
import CHeader from "@/components/CHeader.vue";
import CFoot from "@/components/CFoot.vue";
import CRight from "@/components/CRight.vue";

export default {
    name: "app",
    data() {
        return {
            // 屏幕宽度
            screenWidth: '',
            //屏幕高度
            screenHeight: 'auto',
            // 定义滚动条默认位置
            scrollTop: 0,
            // 右侧显示时的定位值
            showRightPosition: '-100%',
            // 右侧SVG状态
            rightSvgStatus: false,
            // 判断是否为移动端打开
            isMobile: false
        };
    },
    computed: {
        httpState() {
            return this.$store.state.httpState;
        },
        // 返回右侧显示状态
        isShowRight() {
            return this.$store.state.isShowRight;
        },
        // 返回右侧完全显示状态
        isShowAllRight() {
            return this.$store.state.isShowAllRight;
        }
    },
    watch: {
        // 监听并提示全局报错
        httpState(val) {
            this.$snotify.error(val);
        },
        // 右侧栏显示控制
        isShowRight(val) {
            if (val === true) {
                this.showRightPosition = 372 - this.screenWidth + "px";
            } else {
                this.showRightPosition = '-100%';
            }
            this.screenHeight = 'auto';
            this.rightSvgStatus = !this.rightSvgStatus;
        },
        // 右侧栏完全展开控制
        isShowAllRight(val) {
            if (val === true) {
                this.showRightPosition = "0";
                this.screenHeight = document.documentElement.clientHeight - 174 - 141 + 'px';
            }
        }
    },
    components: {
        CHeader,
        CFoot,
        CRight
    },
    methods: {
        // 判断是否为手机端
        fIsMobile() {
            return navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
        },

        // 登录状态判断及相关参数设置
        fIsLogin() {
            if (localStorage.getItem("DLZEdToken")) {
                // 获取用户数据
                this.$api.user
                    .getUserModel()
                    .then(res => {
                        // 本地保存userInfo
                        localStorage.setItem(
                            "DLZEdUserModel",
                            JSON.stringify(res.data.data)
                        );
                        // vuex改变全局登录状态
                        this.$store.commit("changeLoginStatus", true);
                    })
                    .catch(error => {
                        // vuex改变全局登录状态
                        this.$store.commit("changeLoginStatus", false);
                    });
                // vuex改变全局登录状态
                this.$store.commit("changeLoginStatus", true);
            } else {
                // vuex改变全局登录状态
                this.$store.commit("changeLoginStatus", false);
            }
        },
        // 获取屏幕宽度
        getScreenWidth() {
            this.screenWidth = document.body.clientWidth;
        },

        // 右侧栏弹出/收回
        rightToggle() {
            this.$store.commit("changeIsShowRight", !this.isShowRight);
        },
        // 关闭右侧栏
        closeRight() {
            this.$store.commit("changeIsShowRight", false);
        },
        // 完全展开右侧栏
        allShow() {
            this.$store.commit("changeIsShowAllRight", true);
        },
        // 关闭右侧栏（用于全展开）
        closeAllShow() {
            this.$store.commit("changeIsShowAllRight", false);
        }
    },
    mounted() {

         this.isMobile = this.fIsMobile();
         console.log(this.isMobile);
        // 控制页面初次加载动画
        document.getElementById("animation").style.opacity = "0";
        document.getElementById("first-loading").style.opacity = "0";
        setTimeout(function () {
            document.getElementById("animation").style.display = "none";
        }, 500);
        setTimeout(function () {
            document.getElementById("first-loading").style.display = "none";
        }, 1000);
        if (this.isMobile) {
            this.$router.replace('/mIndex');
        }else{
            this.$router.replace('/');
        }

        this.getScreenWidth();
        window.onresize = this.$CustomPatch.throttle(this.getScreenWidth, 0, 3000);
        // 首次加载领域板块淡入
        this.$store.commit("changeShowSortNav", true);
        this.fIsLogin();
    }
};
</script>
<style src="./common/css/App.css">
</style>
<style src="./common/css/Plugin/vuejs-paginate.css">
</style>
