/**
 * curriculum 模块接口列表
 */

// 导入接口域名列表
import base from "../base";
import axios from "../http"; // 导入http中创建的axios实例

const curriculum = {
  // 获取领域列表
  getDomainList() {
    return axios.get(`${base.produce}/Curriculum/GetDomainList`);
  },
  getRecommendList(){
    return axios.get(`${base.produce}/Curriculum/GetRecommendList`);
  },
  // 获取课程列表
  getCurriculumList(params) {
    return axios.get(`${base.produce}/Curriculum/GetCurriculumList`, {
      params
    });
  },
  // 获取课程详情
  getCurriculumModel(params) {
    return axios.get(`${base.produce}/Curriculum/GetCurriculumModel`, {
      params
    });
  },
  // 获取章节实体
  getChapterModel(params) {
    return axios.get(`${base.produce}/Curriculum/GetChapterModel`, { params });
  },
  // 获取问答列表
  getQuestionList(params) {
    return axios.get(`${base.produce}/Curriculum/GetQuestionList`, { params });
  },
  // 提交问题
  submitQuestion(params) {
    return axios.post(
      `${base.produce}/Curriculum/SubmitQuestion`,
      JSON.stringify(params)
    );
  },
  // 回答问题
  answerQuestion(params) {
    return axios.post(
      `${base.produce}/Curriculum/AnswerQuestion`,
      JSON.stringify(params)
    );
  }
};

export default curriculum;
