/**
 * verify 模块接口列表
 */

// 导入接口域名列表
import base from "../base";
import axios from "../http"; // 导入http中创建的axios实例

const verify = {
  /**
   * 手机号密码登录
   * @param params {Object} 注册信息
   * @returns {*}
   */
  getVerify(params) {
    return axios.get(`${base.produce}/Verify/Sms`, { params });
  }
  // 拼接参数调用 如果用{params}就是Query String类型传参
  // getVerify (params) {
  //   return axios.get(`${base.produce}/Verify/Sms?Data.PhoneNumber=${params.PhoneNumber}&Data.VerifyType=${params.VerifyType}`);
  // }
};
export default verify;
