import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import axios from "axios";
import VueAxios from "vue-axios";
import api from "./api/index"; // 导入api接口
import VueAwesomeSwiper from "vue-awesome-swiper/src"; // 轮播插件
import Snotify, {SnotifyPosition, SnotifyToast, SnotifyToastConfig} from "vue-snotify"; // 多功能提示插件
import vRegion from "v-region"; //城市级联选择器
import Element from 'element-ui'; // elementUI
import VueQriously from "vue-qriously"; // 绘制二维码插件
import VuePlyr from "vue-plyr"; // 视频播放器
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome"; // FontAwesome字体图标
import {library} from "@fortawesome/fontawesome-svg-core"; //FontAwesome字体图标
import {
  faSearch,
  faRegistered,
  faUserCircle,
  faSchool,
  faHome,
  faMobile,
  faShieldAlt,
  faMars,
  faVenus,
  faPortrait,
  faEnvelope,
  faVenusMars,
  faTree,
  faMapMarkerAlt,
  faSuitcase,
  faKey,
  faTimes,
  faCartPlus,
  faArrowLeft,
  faAlignJustify
} from "@fortawesome/free-solid-svg-icons"; // 按实际需求导入图标
import CustomPatch from "./common/js/CustomPatch"; //自定义的补充方法

library.add(
  faSearch,
  faRegistered,
  faUserCircle,
  faSchool,
  faHome,
  faMobile,
  faShieldAlt,
  faMars,
  faVenus,
  faPortrait,
  faEnvelope,
  faVenusMars,
  faTree,
  faMapMarkerAlt,
  faSuitcase,
  faKey,
  faTimes,
  faCartPlus,
  faArrowLeft,
  faAlignJustify
);

// 自定义组件

// 插件样式
import "swiper/dist/css/swiper.css";
import "vue-snotify/styles/material.css";
import "vue-plyr/dist/vue-plyr.css";
import './common/css/element-#1CABA0/index.css';// 自定义的elementUI主题

Vue.use(vRegion);
const snotifyOptions = {
  toast:{
    timeout: 2000,
    showProgressBar: true,
    closeOnClick: true,
    pauseOnHover: true,
    position: SnotifyPosition.rightTop
  }
};
Vue.use(Snotify, snotifyOptions);
Vue.use(VueAxios, axios);
Vue.use(VueAwesomeSwiper);
Vue.use(Element);

Vue.use(VueQriously);
Vue.use(VuePlyr);
Vue.config.productionTip = false;


Vue.component("font-awesome-icon", FontAwesomeIcon);

// 路由守卫
router.beforeEach((to, from, next) => {
  // chrome
  document.body.scrollTop = 0;
  // firefox
  document.documentElement.scrollTop = 0;
  // safari 暂不开启 会影响城市选择器动态定位计算
  // window.pageYOffset = 0;
  // 路由跳转自动关闭右侧栏
  store.commit("changeIsShowRight", false);
  store.commit("changeIsShowAllRight", false);
  next()
});


Vue.prototype.$api = api; // 将api挂载到vue的原型上
Vue.prototype.$CustomPatch = CustomPatch; // 将自定义补充挂载到vue的原型上
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
