          position: "微软（中国）云计算解决方案资深技术专家",
          des:
            "男,1970年出生,华东理工大学计算机软件学士,北方交大EMBA,曾就职于联想、惠普、VMWARE等公司，目前在微软公司担任数据中心及架构高级解决方案专家，有25年以上的IT行业经验。在微软公司参与过重庆渝北大数据平台，四川省教育厅的公共资源平台，四川省省市级电子政务平台和四川省环保数据中心及污染源监测等项目。帮助吉峰农机、科伦药业全友家私和新希望集团等企业构建混合云，实现数字化转型。",
          background:
            "background:url(" +
            require("../common/images/t1.png") +
            ") center center no-repeat"
        },
        {
          name: "程尊华",
          position: "现任MCT Regional Lead资深微软认证讲师（MCT）",
          des:
            "从业17年中累计提供了10000余小时的培训和技术讲座服务，培训人次8000多人，均获得良好的反馈。曾在微软担任解决方案专家。目前专注于云平台，云原生应用，大数据、机器学习以及AI领域。",
          background:
            "background:url(" +
            require("../common/images/t2.png") +
            ") center center no-repeat"
        },
        {
          name: "许豪",
          position: "微软（中国）公司开发平台资深技术专家",
