            "现任微软（中国）公司开发平台资深技术专家，负责微软开发与测试技术的推广和导入工作。致力于新兴开发测试技术的研究，推动先进工具和方法论的导入和使用，为行业培养输送新型人才。在加入微软之前，曾任惠普（HP）ALM架构师、内训讲师；诺基亚（Nokia）手机测试经理；通用电气（GE）测试技术专家。曾担任上海市软体行业协会荣誉讲师。",
          background:
            "background:url(" +
            require("../common/images/t3.png") +
            ") center center no-repeat"
        },
        {
          name: "彭爱华",
          position: "微软云平台解决方案专家资深讲师",
          des:
            "网名盆盆，国内著名技术达人，微软云计算解决方案专家，《Window 7使用详解》的作者。11届微软最有价值专家，国内第一位虚拟化方向的MVP（全球共有9名）。曾荣获微软TechEd最佳讲师、首届IT管理技术大会的最佳讲师称号，Windows Server 2012中国发布会讲师。参与多家大型汽车企业、大型银行、大型外企、政府部门、酒店集团、医院等项目的设计、规划、部署和实施。",
          background:
            "background:url(" +
            require("../common/images/t4.png") +
            ") center center no-repeat"
        },
        {
          name: "庄俊乾",
          position: "微软（中国）有限公司云计算开发领域解决方案技术专家",
          des:
