          background:
            "background:url(" +
            require("../common/images/t5.png") +
            ")  center center no-repeat"
        },
        {
          name: "祁清华",
          position: "大数据专家微软金牌讲师",
          des:
            "作为一名MCT讲师，长期从事微软专职讲师工作。在此期间也积极参加微软的各种活动，及企业数据分析和展现的部署实施。曾在多家企业IT部门任职，曾经担任工程师，企业讲师IT部门负责人工作。有丰富的企业管理和实践经验。",
          background:
            "background:url(" +
            require("../common/images/t6.png") +
            ") center center no-repeat"
        }
