          imgUrl: require("../common/images/AI.jpg"),
          tit: "人工智能",
          path: "/courseCenter/courseList/01"
        },
        {
          imgUrl: require("../common/images/dataScience.jpg"),
          tit: "前沿技术",
          path: "/courseCenter/courseList/02"
        },
        {
          imgUrl: require("../common/images/cloudComputing.jpg"),
          tit: "云计算&大数据",
          path: "/courseCenter/courseList/03"
        },
        {
          imgUrl: require("../common/images/bigData.jpg"),
          tit: "算法&数学",
          path: "/courseCenter/courseList/04"
        }
      ],
      // 推荐课程列表
      recommendList: [],
    };
  },
  computed: {
    showSortNav() {
      return this.$store.state.showSortNav;
    },
    showCourseNav() {
      return this.$store.state.showCourseNav;
    },
    showCertificate() {
      return this.$store.state.showCertificate;
    },
    // 返回右侧完全显示状态
    isShowAllRight() {
      return this.$store.state.isShowAllRight;
    }
  },
  watch: {
    // 当右侧栏全部展开时需要卸载主页滑动监听事件，避免报错（虽然不影响页面效果）
    isShowAllRight(val) {
      if (val === true) {
        window.removeEventListener('scroll', this.homePageScroll, true);
      } else {
        window.addEventListener('scroll', this.homePageScroll, true);
      }
    }
  },
  methods: {
    // 获取推荐课程
    getRecommendList() {
      this.$api.curriculum.getRecommendList()
        .then(res => {
          this.recommendList = res.data.data;
        })
        .catch(error => {
          console.log(error)
        })
    },
    /**
     * 前往课程详情
     * @param curriculumId 课程ID
     */
    toCourseDetails(curriculumId) {
      this.$router.push({
        path: `/courseCenter/courseDetails/${curriculumId}`
      })
    }
  },
  mounted() {
    this.getRecommendList();
    this.homePageScroll = () => {
      if (document.documentElement.scrollTop === false &&
        window.pageYOffset === false &&
        document.body.scrollTop === false &&
        document.querySelector(this.el).scrollTop === false) {
        this.scrollTop = 0;
      } else {
        this.scrollTop = document.documentElement.scrollTop ||
          window.pageYOffset ||
          document.body.scrollTop ||
          document.querySelector(this.el).scrollTop;
        if (this.scrollTop >= 0 && this.scrollTop <= 300) {
          this.$store.commit("changeShowSortNav", true);
        } else if (this.scrollTop > 300 && this.scrollTop <= 700) {
          this.$store.commit("changeShowCourseNav", true);
        } else if (this.scrollTop > 700 && this.scrollTop <= 1500) {
          this.$store.commit("changeShowCertificate", true);
          window.removeEventListener('scroll', this.homePageScroll, true);
        }
      }
    };
    // 监听滚动事件
    window.addEventListener('scroll', this.homePageScroll, true);
    // 联动轮播控制
    this.$nextTick(() => {
      const swiperTop = this.$refs.swiperTop.swiper;
      const swiperThumbs = this.$refs.swiperThumbs.swiper;
      swiperTop.controller.control = swiperThumbs;
      swiperThumbs.controller.control = swiperTop;
    });
    // this.$nextTick(() => {
    //   const swiperTop2 = this.$refs.swiperTop2.swiper;
    //   const swiperThumbs2 = this.$refs.swiperThumbs2.swiper;
    //   swiperTop2.controller.control = swiperThumbs2;
    //   swiperThumbs2.controller.control = swiperTop2;
    // });
